angular.module("MyApp", ["PageM", 'ToolM',"cfp.hotkeys"])
         .controller("MyController", ["$scope", "PageProvider","ToolProvider", "$http", "hotkeys", function ($scope, PageProvider,TPM, $http, hotkeys) {

             $scope.PM = PageProvider;
             $scope.TM = {};
             //$scope.TM.isHide = true;
             $scope.VM = {};
             //$scope.VM.location = "";
             //$scope.VM.locationID = 0;
             $scope.VM.locationObj = {};
             $scope.VM.locationObj.text = "";
             $scope.VM.locationObj.id = 0;
             $scope.VM.agencyObj = {};
             $scope.VM.agencyObj.AN = "";
             $scope.VM.agencyObj.I =  0;

             //$scope.VM.agency = "";
             //$scope.VM.agencyID = 0;
             $scope.VM.title = "";

             $scope.VM.publishStateAry = [].concat(AgState);
             $scope.VM.CountyAry = [];
             $scope.VM.TownAry = [];
             $scope.VM.VillageAry = [];

             $scope.VM.publishState = "";
             $scope.VM.SelectCounty = "0";
             $scope.VM.SelectTown = "0";
             $scope.VM.SelectVillage = "0";
           
             //$("#tmBlock").show();
             //$scope.PM.changePage("", tbData, function (data) {
             //    $scope.TM.data = data.message;
             //    history.replaceState({ "pageIndex": 1 }, document.title, location.href);
             //});
             //$scope.changePage(1);
             $scope.doReset = function () {
                 var element = document.querySelector('#ucOrgName');
                 element.value = "";
                 var element = document.querySelector('#ucAgencyName');
                 element.value = "";
                 $scope.VM.SelectCounty = "0";
                 $scope.VM.SelectTown = "0";
                 $scope.VM.SelectVillage = "0";
                 $scope.VM.publishState = "";
                 $scope.VM.agencyObj.AN = "";
                 $scope.VM.locationObj.id = 0;
                 $("#tmBlock").hide();
                 $scope.PM.reset();
                 $('#divFunc').hide();
                 window.setOrgID(defaultOrgID);
                 window.setOrgName(defaultOrgName);
             };

             $scope.queryClick = function () {
                 $('#btnSubmit').trigger('click');
                 if (document.querySelector("#MyForm").checkValidity()) {
                     $scope.changePage(1);
                 }
             };

             $scope.changePage = function (pageIndex, noHistory) {
                 $("#tmBlock").show();
                 $("#divFunc").show();
                 var pgData = $scope.PM.genPageData(pageIndex);
                 var postData = {};

                 postData["oid"] = window.getOrgID();
                 if (window.getOrgName()=="")
                 {
                     postData["oid"] = 0;
                 }
                 postData["ac"] = $scope.VM.SelectCounty;
                 postData["at"] = $scope.VM.SelectTown;
                 postData["av"] = $scope.VM.SelectVillage;
                 postData["an"] = window.getAgencyName();
                 postData["as"] = $scope.VM.publishState;

                 postData = $scope.PM.filterPageData(pgData, postData);

                 $scope.PM.changePage("/Vaccination/ParameterM/LocationSettingOP.aspx", postData, function (data) {
                     $scope.TM.data = data.message;
                     $scope.$apply(function () { });

                     if (history.pushState && noHistory == undefined) {
                         if (pageIndex > 1 || location.hash.length > 0) {
                             history.pushState({ "pageIndex": pageIndex }, document.title, location.href.split("?")[0] + "?#h");
                         }
                         else {
                             history.replaceState({ "pageIndex": 1 }, document.title, location.href);
                         }
                     }
                 });
             };

             //$scope.changePage(1);

             hotkeys.add({
                 combo: 'enter',
                 description: 'Description goes here',
                 allowIn: ['INPUT', "BUTTON", 'SELECT', 'TEXTAREA'],
                 callback: function (event, hotkey) {
                     var focusType = document.activeElement.type;
                     if (focusType == "text" || focusType == "select-one") {
                         $('#btnSubmit').trigger('click');
                         if (document.querySelector("#MyForm").checkValidity()) {
                             $scope.changePage(1);
                         }
                     }
                 }
             });

             $scope.BindCounty = function () {
                 var postData = {};
                 postData["a"] = "County";

                 $http({
                     method: 'POST',
                     url: "/Ashx/SystemAreaCodeOP.ashx",
                     data: $.param(postData),
                     headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
                 })
                .success(function (data, status, headers, config) {
                    data = data || [];
                    if (data.length > 0) {
                        $scope.VM.CountyAry = [].concat(data);
                        $scope.VM.SelectCounty = "0";
                    }
                    else {
                        $scope.VM.CountyAry = [];
                        $scope.VM.SelectCounty = "0";
                    }
                })
                .error(function (data, status, headers, config) {
                });
                
             };

             $scope.BindCounty();

             $scope.SelectCountyChange = function () {
                 var postData = {};
                 postData["a"] = "Town";
                 postData["p"] = $scope.VM.SelectCounty;

                 $http({
                     method: 'POST',
                     url: "/Ashx/SystemAreaCodeOP.ashx",
                     data: $.param(postData),
                     headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
                 })
                .success(function (data, status, headers, config) {
                    data = data || [];
                    if (data.length > 0) {
                        $scope.VM.TownAry = [].concat(data);;
                        $scope.VM.SelectTown = "0";
                    }
                    else {
                        $scope.VM.TownAry = [];
                        $scope.VM.SelectTown = "0";
                    }

                    $scope.VM.VillageAry = [];
                    $scope.VM.SelectVillage = "0";
                })
                .error(function (data, status, headers, config) {
                });
                
             };

             $scope.SelectTownChange = function () {
                 var postData = {};
                 postData["a"] = "Village";
                 postData["p"] = $scope.VM.SelectTown;

                 $http({
                     method: 'POST',
                     url: "/Ashx/SystemAreaCodeOP.ashx",
                     data: $.param(postData),
                     headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
                 })
               .success(function (data, status, headers, config) {
                   data = data || [];
                   if (data.length > 0) {
                       $scope.VM.VillageAry = [].concat(data);
                   }
                   else {
                       $scope.VM.VillageAry = [];

                   }
                   $scope.VM.SelectVillage = "0";
               })
               .error(function (data, status, headers, config) {
               });
             };

             //$scope.openOrgs = function () {
             //    TPM.openWindowWithPost("/SelectSingleOrg.aspx", "SelectSingleOrg", 620, 450);
             //};

             //$scope.openSelectAgency = function () {
             //    TPM.openWindowWithPost("/Vaccination/ParameterM/LocationSetting_SelectAgency.aspx", "SelectAgency", 930, 450);
             //};

             $scope.goDetail = function (record) {
                 var data = $scope.save() || "";

                 var obj = {};
                 obj['i'] = record["I"];
                 obj['hash'] = encodeURIComponent(data);
                 $(document).on("click", "form");
                 TPM.doPOST("/Vaccination/ParameterM/LocationSetting_Detail.aspx", obj,document.querySelector("#MyForm"));
             };

             $scope.goPrint = function () {
                 var data = '<section class="Content"><div class="listTb">';
                 data += $("#tmBlock").html();
                 data += '</section></div>';
                 TPM.popPrint(data, ["/css/design.css"]);
             };

             $scope.save = function () {
                 var data = {};
                 data["OrgID"] = window.getOrgID();
                 data["OrgName"] = window.getOrgName();
                 data["AgencyID"] = window.getAgencyID();
                 data["AgencyName"] = window.getAgencyName();
                 data["publishState"] = $scope.VM.publishState;
                 data["SelectCounty"] = $scope.VM.SelectCounty;
                 data["SelectTown"] = $scope.VM.SelectTown;
                 data["SelectVillage"] = $scope.VM.SelectVillage;
                 data['pgNow'] = $scope.PM.pgNow;
                 TPM.setData(data);
                 return JSON.stringify(data);
             };

             if (location.hash.length > 0) {
                 var data = TPM.getData();
                 $scope.VM.SelectCounty = data.SelectCounty;
                 $scope.VM.SelectTown = data.SelectTown;
                 $scope.VM.SelectVillage = data.SelectVillage;
                 $scope.VM.publishState = data.publishState;
                 $("#ucOrgID").val(data.OrgID);
                 $("#ucOrgName").val(data.OrgName);
                 $("#ucAgencyID").val(data.AgencyID);
                 $("#ucAgencyName").val(data.AgencyName);
                 $scope.changePage(data.pgNow);
             }
             else {
                 $scope.doReset();
             }
}]);

